.comic-store {
  text-align: center;
}

.comic-store-logo {
  -webkit-animation: comic-store-logo-spin infinite 20s linear;
          animation: comic-store-logo-spin infinite 20s linear;
  height: 80px;
}

.comic-store-header {
  background-color: #222;
  padding: 20px;
  color: white;
}

.comic-store-intro {
  font-size: large;
}

@-webkit-keyframes comic-store-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes comic-store-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.thumbnail{
    width:100%;
    max-width:100px;
    min-width: 100px;
    min-height: 100px;
}

.thumbnail-large{
    width:100%;
    max-width:180px;
    min-width: 180px;
    min-height: 180px;
}



